import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewBannerPageComponent } from './new-banner-page.component';

const routes: Routes = [
  { path: '', component: NewBannerPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewBannerPageRoutingModule { }
