import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { BannerService } from '../../angular-services/banner.service';
import { MediaService } from '../../angular-services/media.service';

@Component({
  selector: 'app-new-banner-form-admin',
  templateUrl: './new-banner-form.component.html',
  styleUrls: ['./new-banner-form.component.css']
})
export class NewBannerFormComponent implements OnInit {

  formNewBanner: FormGroup;
  mediaType = 'banners';
  alert: any = { type: '', message: '', show: false };
  urlObjects: string = 'https://viruscursinho.com.br:3002/banners';
  showModalSelectFile: boolean = false;
  tabIndex: number = 1;
  objects: any[] = [];
  sizePage: number = 8;
  pageSelected: number = 0;
  pages: any[] = [];

  constructor(private bannerService: BannerService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formNewBanner = new FormGroup({
      title: new FormControl('', Validators.required),
      description: new FormControl(''),
      photo: new FormControl('', Validators.required),
      url: new FormControl(''),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewBanner.valid) {
      let data = this.formNewBanner.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.bannerService.addBanner(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formNewBanner.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
